.news__container {
  position: relative;
  max-width: $max-width;
  margin: 0 auto;
  padding: 120px 100px;

  @include vp-1199 {
    padding: 100px 45px;
  }
}

.news h2 {
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;
  color: $color-secondary;
  text-align: center;
  margin: 0;
  margin-bottom: 48px;

  @include vp-1199 {
    font-size: 34px;
    margin-bottom: 30px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 24px;
  }
}

.news__list {
  display: flex;
  justify-content: flex-start;
  gap: 30px;
  margin: 0 auto;
  width: fit-content;
  flex-wrap: wrap;
  margin-bottom: 38px;

  @include vp-1199 {
    gap: 10px;
  }
}

.news__item {
  padding: 10px 26px;
  opacity: 0.6;

  @include vp-1199 {
    padding: 10px 20px;
  }

  @include vp-767 {
    padding: 6px 14px;
  }
}

.news__item--active {
  border: 1px solid $color-secondary;
  border-radius: 20px;
  opacity: 1;
}

.news__item a {
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  color: $color-secondary;

  @include vp-767 {
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.24px;
  }
}

.news .swiper-slide {
  border-radius: 12px;
  width: 286px;

  @include vp-1199 {
    width: auto;
  }
}

.news .swiper-slide:first-child{
  width: 604px;

  @include vp-1199 {
    width: auto;
  }
}

