.news__content-container {
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin: 0 46px -1px -1px;

  @include vp-1199 {
    min-height: 350px;
  }

  @include vp-767 {
    min-height: 240px;
  }
}

.news .swiper-slide:first-child .news__content-container {
  margin-right: 215px;

  @include vp-1199 {
    margin-right: 46px;
  }
}

.news__date-container {
  position: relative;
  padding: 12px 12px 6px 0;
  background-color: $color-secondary-beige;
  border-radius: 0px 12px 0px 0px;
  width: fit-content;
  margin-bottom: -1px;
}

.news__date-container span {
  display: block;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  color: $color-secondary;
}

.news__date-container .news__auxiliary-icon:first-child {
  position: absolute;
  left: 0;
  top: -15px;
  fill: $color-secondary-beige;
}

.news__date-container .news__auxiliary-icon:last-child {
  position: absolute;
  right: -15px;
  bottom: 0;
  fill: $color-secondary-beige;
}

.news__title-container {
  position: relative;
  padding: 5px 10px 0 0;
  background-color: $color-secondary-beige;
  border-radius: 0px 12px 0px 0px;
  margin-right: 26px;
}

.news .swiper-slide:first-child .news__title-container {
  margin-right: 98px;
  padding: 15px 18px 0 0;
}

.news__title-container p {
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  color: $color-secondary;
  margin: 0;

  @include vp-1199 {
    font-size: 20px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 18px;
  }
}

.news__description {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 16px;
  margin-top: -1px;
  padding: 9px 18px 12px 0;
  border-radius: 0px 12px;
  background-color: $color-secondary-beige;

  @include  vp-1199 {
    padding: 9px 18px;
  }

  @include vp-767 {
    min-height: 69px;
  }
}

.news__description p {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin: 0;

  @include vp-1199 {
    line-height: 20px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 14px;
  }
}

.news__description a {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  background-color: $color-secondary;
  border-radius: 50%;
}

.news__auxiliary-icon {
  position: absolute;
  right: -15px;
  bottom: 0;
  fill: $color-secondary-beige;
}

.news__pagination-container {
  width: fit-content;
  margin: 42px auto 0 auto;
  padding: 0 20%;
}

.news__swiper-pagination {
  display: flex;
}

.news .swiper-pagination-bullet {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: $ff-manrope;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: $color-secondary;
  opacity: 0.5;
  border-radius: 0;
  border: none;
}

.news .swiper-pagination-bullet-active {
  border-radius: 50%;
  background-color: $color-secondary;
  color: $color-default-white;
  font-family: $ff-manrope;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  opacity: 1;
}

.news .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet:not(:last-child) {
  margin: 0;
  margin-right: 20px;
}

.news .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet:last-child {
  margin-right: 0;
}

.news__buttons-container {
  display: flex;
  justify-content: space-between;
  position: absolute;
  right: 100px;
  left: 100px;
  bottom: 124px;
}

.news .swiper-grid-column>.swiper-wrapper {
  flex-direction: row;
}
