  font-family: "Manrope", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #316dc2;
  opacity: 0.5;
  border-radius: 0;
  border: none;
}

.news .swiper-pagination-bullet-active {
  border-radius: 50%;
  background-color: #316dc2;
