@media (max-width: 1199px) {
  .questions__container {
    padding: 0 45px;
  }
}

.questions h2 {
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;
  color: #316dc2;
  text-align: center;
  margin: 0;
  margin-bottom: 70px;
}
@media (max-width: 1199px) {
  .questions h2 {
    font-size: 34px;
    margin-bottom: 41px;
  }
}
@media (max-width: 767px) {
  .questions h2 {
    font-size: 20px;
    line-height: 20px;
  }
}

.accordion {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.accordion .accordion {
  width: 100%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 120px;
}
@media (max-width: 1199px) {
  .accordion__element:last-child {
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .accordion__element:last-child {
    margin-bottom: 70px;
  }
}
.accordion__element {
  margin: 0;
  padding: 40px 40px 29px 40px;
  border: 1px solid #316dc2;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .accordion__element {
    padding: 30px 29px 28px 29px;
  }
}
@media (max-width: 767px) {
  .accordion__element {
    padding: 21px 0 18px 0;
  }
}
.accordion__element.is-active {
  background-color: #316dc2;
  padding-bottom: 30px;
}
@media (max-width: 1199px) {
  .accordion__element.is-active {
    padding-bottom: 27px;
  }
}
.accordion__element.is-active .accordion__wrapper p {
  color: #f0efe9;
}
.accordion__element.is-active .accordion__button-container span {
  color: #f0efe9;
}
.accordion__element.is-active .accordion__button {
  background-color: #ffffff;
}
.accordion__element.is-active .accordion__button::after {
  content: "";
  width: 18px;
  height: 1px;
  background-color: #316dc2;
}
.accordion__button {
  background-color: #316dc2;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin-left: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.accordion__button:disabled {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .accordion__button {
    width: 32px;
    height: 32px;
    min-width: 32px;
  }
}
.accordion__wrapper {
  padding: 0;
}

.accordion__element.is-active .accordion__close-button-icon {
  display: none;
}

.accordion__close-button-icon {
  stroke: #ffffff;
}

.accordion__button:hover .accordion__close-button-icon,
.accordion__button:focus-visible .accordion__close-button-icon {
  stroke: #ffffff;
  outline: none;
}

.accordion__button-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  background-color: transparent;
  width: 100%;
  padding: 0;
}
.accordion__button-container:hover .accordion__close-button-icon, .accordion__button-container:focus-visible .accordion__close-button-icon {
  stroke: #ffffff;
  outline: none;
}

.accordion__button-container span:first-child {
  width: fit-content;
  text-align: left;
  color: #316dc2;
  font-family: "Manrope", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 28px;
  margin-right: 32%;
}
@media (max-width: 1199px) {
  .accordion__button-container span:first-child {
    font-size: 22px;
    line-height: 26px;
    margin-right: 14%;
  }
}
@media (max-width: 767px) {
  .accordion__button-container span:first-child {
    font-size: 16px;
    line-height: 18px;
  }
}

.accordion__wrapper p {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  margin: 0;
  padding-top: 14px;
}
@media (max-width: 1199px) {
  .accordion__wrapper p {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .accordion__wrapper p {
    font-size: 14px;
  }
}

.accordion__content {
  padding-right: 30%;
}
@media (max-width: 1199px) {
  .accordion__content {
    padding-right: 14%;
  }
}
@media (max-width: 767px) {
  .accordion__content {
    padding-right: 0;
  }
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

